<template>
  <div>
    <van-nav-bar title="购物车" left-arrow @click-left="$router.go(-1)" />
    <div>
      <van-card
        v-for="item in cartList"
        :key="item.id"
        :price="item.price"
        :title="item.goodsname"
        :thumb="$preImg + item.img"
      >
        <template #footer>
          <van-stepper
            theme="round"
            button-size="22"
            disable-input
            v-model="item.num"
            id="add"
          />
          <van-button type="primary" size="mini" @click="del(item.id)" id="del"
            >删除</van-button
          >
        </template>
      </van-card>
    </div>
    <van-submit-bar :price="total" button-text="提交订单" />
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { delCart } from "../utils/request";

export default {
  data() {
    return {
      // total: 0,
    };
  },
  methods: {
    ...mapActions({
      requestCartList: "cartListActions",
    }),
    del(id) {
      // 发起删除购物车请求
      delCart({ id }).then((res) => {
        // 1.提示删除成功
        this.$toast.success("删除商品成功");
        // 2.发起更新购物车列表
        this.requestCartList(this.user.uid);
      });
    },
  },
  computed: {
    ...mapGetters({
      user: "user",
      cartList: "cartList",
      total: "total",
    }),
  },
  mounted() {
    if (this.user) {
      this.requestCartList(this.user.uid);
    }
  },
};
</script>

<style scoped>
.van-submit-bar {
  position: fixed;
  bottom: 50px;
  right: 0;
}

.van-card {
  position: relative;
}
#add {
  position: absolute;
  right: 10px;
  top: 40px;
}
#del {
  width: 80px;
  position: absolute;
  right: 10px;
  bottom: 10px;
  border-radius: 5px;
}
</style>


